import React, { Component, Fragment } from 'react';
import pelota from '../../Imagenes/pelota.png';
import './Partido_tv.css';



class Partido_tv extends Component {

    constructor(props) {
        super(props)
		this.state = {
            id:'',
            Partido: []
		}
       
    }

 

    render(){


        const { partido } = this.props;
  
        const infoPartido =partido;
        
const {resultado} = infoPartido;
let horade_inicio;
//const {puntos_j1} = resultado;
var juegosj1,claseSaque2,claseSaque1;
var puntos_j1,juegos_j1,set1_j1,set2_j1,set3_j1,saque_actual,sets_j1,ganador,jugador1,jugador3;
var puntos_j2,juegos_j2,set1_j2,set2_j2,set3_j2,sets_j2;
if(resultado){
    
    jugador1=infoPartido.jugador1.nombre;
    jugador3=infoPartido.jugador3.nombre;


    horade_inicio=resultado.hora_inicio;
    puntos_j1=resultado.puntos_j1;
    juegos_j1=resultado.juegos_j1;
    set1_j1=resultado.set1_j1;
    set2_j1=resultado.set2_j1;
    set3_j1=resultado.set3_j1;
    saque_actual=resultado.saque_actual;
    sets_j1=resultado.sets_j1;
    ganador=resultado.ganador;
    puntos_j2=resultado.puntos_j2;
    juegos_j2=resultado.juegos_j2;
    set1_j2=resultado.set1_j2;
    set2_j2=resultado.set2_j2;
    set3_j2=resultado.set3_j2;
    sets_j2=resultado.sets_j2;
 
    
}
if(saque_actual===1){
    claseSaque2="iconoSaque_hidden";
}else{
    claseSaque1="iconoSaque_hidden";
}

console.log(horade_inicio)

   
     
        return (
        <Fragment>
		<div className="contenedor">
            <div className="jugador">
                <div className="nombre">
                <span className="nombre_jugador">{jugador1}</span>
                <div className="saque"><img className={`pelota ${claseSaque1}`} src={pelota} alt=""/></div>
                <div className="puntos">{puntos_j1}</div>
                </div>
               
                
                <div className="set1">{set1_j1}</div>
                <div className="set2">{set2_j1}</div>
                <div className="set3">{set3_j1}</div>
            </div>
            <div className="jugador">
                <div className="nombre">
                <span className="nombre_jugador">{jugador3}</span>
                <div className="saque"><img className={`pelota ${claseSaque2}`} src={pelota} alt=""/></div>
                <div className="puntos">{puntos_j2}</div>
                </div>
                
               
                <div className="set1">{set1_j2}</div>
                <div className="set2">{set2_j2}</div>
                <div className="set3">{set3_j2}</div>
            </div>
        </div>
		</Fragment>
        );		

}
}
export default Partido_tv;